class Clients extends React.Component {

	makeCompanies() {
		const images = imports.map((logo, index) =>
            <div key={index} className="logo-container">
            	<img className="logo" src={logo.file} />
            	<div className="overlay">
            		<div className="text"> {logo.name} </div>
            	</div>
            </div>
        );
        return images;
	}

	render() {
		return (
			<div>
				<div className="section-intro" id="clients-intro"><h1>Our clients</h1></div>
				<div className="allLogos">
					{this.makeCompanies()}
				</div>
			</div>

			/*<div>
				<div className="section-intro" id="team-intro"><h1>Our clients</h1></div>
				<div className="allLogos">
					{this.makeCompanies()}
				</div>
			</div>*/
		)
	}
}

export default Clients;


